import { FormInput } from "@/shared/ui/form-input";
import { inputsContactsData } from "./inputs-contacts.data";
import { CustomInput } from "@/shared/ui/custom-input";
import { Control } from "react-hook-form";
import { IContactsForm } from "../../types/contacts-form";
import { BookingForm } from "../../types/booking-form";

export interface InputsContactsProps {
  control: Control<BookingForm>;
}
export const InputsContacts = ({ control }: InputsContactsProps) => {
  return (
    <div>
      {inputsContactsData.map((elem) => {
        switch (elem.type) {
          case "default":
            return (
              <FormInput<IContactsForm>
                key={elem.name}
                data={elem}
                control={control}
              >
                {(field) => <CustomInput {...field} />}
              </FormInput>
            );

          default:
            return null;
        }
      })}
    </div>
  );
};
