import { useStep } from "@/shared/ui/step-context";
import { useForm } from "react-hook-form";
import { InputsContacts } from "../inputs-contacts";
import { zodResolver } from "@hookform/resolvers/zod";
import { contactSchema } from "../../schemas/contact.schema";
import { verifyCodeSchema } from "../../schemas/verify-code.schema";
import { BookingForm } from "../../schemas";

export const FormBooking = () => {
  const { step } = useStep();

  const { control } = useForm<BookingForm>({
    resolver: zodResolver(step === 1 ? contactSchema : verifyCodeSchema),
    defaultValues: {},
  });
  const renderStep = () => {
    switch (step) {
      case 1:
        return <InputsContacts control={control} />;
      case 2:
        return <div>Step 2 content</div>;
      case 3:
        return <div>Step 3 content</div>;
      default:
        return <div>Unknown step</div>;
    }
  };
  return <div>{renderStep()}</div>;
};
